
import { defineComponent, PropType } from 'vue'
import HisBasicTable from './HisBasicTable.vue'
import {modalController} from '@ionic/vue'
import { 
  IonHeader, 
  IonToolbar, 
  IonContent, 
  IonFooter, 
  IonButton 
} from "@ionic/vue"
export default defineComponent({
  components: { 
    HisBasicTable,
    IonHeader, 
    IonToolbar, 
    IonContent, 
    IonFooter, 
    IonButton 
  },
  props: {
    title: {
      type: String, 
      required: true
    },
    columns: {
      type: Object as PropType<string[]>,
      required: true
    },
    rows: {
      type: Object as PropType<string[]>,
      default: true
    }
  },
  methods: {
    async closeModal() {
      await modalController.dismiss({})
    },
  }
})
